import * as React from 'react';
import logger from '@ali/4ever-logger';
import SpacingSet from './sidebar/spacing';
import IndSet from './sidebar/ind';
import { idleRender } from '@ali/4ever-component';


function ParagraphSidebar(props) {
  const { controller, locale } = props;
  React.useEffect(() => {
    logger.sum('paragraph_sidebar_show');
  }, []);
  return (
    <div>
      <SpacingSet controller={controller} locale={locale} />
      <IndSet controller={controller} locale={locale} />
    </div>
  );
}

export default idleRender(ParagraphSidebar, 400, { leading: true });
